import * as Babel from "@babel/standalone";
import { ParserReturn } from "../type";

const getOptions = (scriptType: string) => {
  const config = {
    presets: [],
    plugins: [],
  };
  switch (scriptType) {
    case "typescript":
      config.plugins.push(["transform-typescript"]);
      break;
    case "react-js":
      config.presets.push("react");
      break;
    case "react-ts":
      config.presets.push("react");
      config.plugins.push([
        "transform-typescript",
        {
          isTSX: true,
        },
      ]);
      break;
  }

  return config;
};

// 添加依赖
const addDependencies = (scriptType: string): Record<string, string> => {
  const imports: Record<string, string> = {};
  switch (scriptType) {
    case "react-js":
    case "react-ts":
      imports.react = "https://esm.sh/react";
      imports["react-dom"] = "https://esm.sh/react-dom";
      break;
  }

  return imports;
};

const babelTransform = (scriptType: string, input: string) => {
  const outputCode = Babel.transform(input, getOptions(scriptType)).code;
  return `<script type="module">${outputCode}</script>`;
};

export const transformScript = (
  scriptType: string,
  input: string
): ParserReturn => {
  try {
    return {
      imports: addDependencies(scriptType),
      code: babelTransform(scriptType, input),
    };
  } catch (err) {
    console.error(err);
    return { imports: {}, code: "" };
  }
};
