import { ParserReturn } from "../type";

export const sassParser = (code: string): ParserReturn => {
  return {
    imports: {
      sass: "https://esm.sh/sass",
    },
    code: `    
    <script type="module">
      import * as sass from 'sass';
    
      let styleEle = document.getElementById("css");
      if (!styleEle) {
        styleEle = document.createElement("style");
        styleEle.id = "css";
        document.head.appendChild(styleEle);
      }
      styleEle.innerHTML = sass.compileString(\`${code}\`)?.css;
    </script>
    `,
  };
};
