const langMap = {
  javascript: async () => await import("@codemirror/lang-javascript"),
  css: async () => await import("@codemirror/lang-css"),
  html: async () => await import("@codemirror/lang-html"),
};

export const getEditorLangModule = async (lang: string) => {
  const result = await langMap[lang]?.();
  if (result) {
    return result?.[lang];
  }
  return undefined;
};
